import PropTypes from 'prop-types'
import { useFronthubContext } from '../services/context'

import * as external from './external'
import * as internal from './internal'
import { useLocation } from './location'

const Routes = ({ children }) => {
  const { pathname, hash } = useLocation()
  const { user = {}, instance = {}, paths: dynamic = {} } = useFronthubContext()
  const { email, preferences = {} } = user
  const { path, psrm } = instance
  const { workVision = 'pipeline' } = preferences

  const routes = {
    ...external,
    ...internal,
    ...dynamic,
    dealsWorkVision: `${internal.deals}/${workVision}`,
    helpCenter: `https://crmsupport.rdstation.com.br/hc/pt-br?path=${path}&email=${email}&psrm=${psrm}`,
  }

  const active = pathname + hash

  const isActive = route => [].concat(route).some(route => route === active)

  return children({ routes, isActive })
}

Routes.displayName = 'Routes'

Routes.propTypes = {
  children: PropTypes.func,
}

export default Routes
