import { useTranslation as useTranslationReact } from 'react-i18next'

const currencyType = {
  'pt-BR': 'BRL',
  'en-US': 'USD',
}

let i18nCreated = false

export const useTranslation = () => {
  const { t, i18n } = useTranslationReact()
  if (!i18nCreated) {
    i18n.init({
      interpolation: {
        format: function (value, format, lng) {
          if (format === 'money') return new Intl.NumberFormat(lng, { style: 'currency', currency: currencyType[lng] }).format(value)
          return value
        },
      },
    })

    i18nCreated = true
  }

  return { t }
}
