import React from 'react'

import { Alert } from '@resultadosdigitais/crm-components'

import { useTranslation } from '../../services/formatter'
import { useFronthubContext } from '../../services/context'

import Routes from '../../routes'

export default () => {
  const { t: translate } = useTranslation()
  const { user = {} } = useFronthubContext()
  const { impersonated } = user

  return (
    <Routes>
      {({ routes }) =>
        impersonated ? (
          <Alert id="impersonated-alert">
            {translate('logged_on_client_account')}
            <Alert.Link href={routes.authImpersonate}>{translate('go_back_to_admin')}</Alert.Link>
          </Alert>
        ) : null
      }
    </Routes>
  )
}
