import React from 'react'
import styled from 'styled-components'
import { useTranslation } from '../../services/formatter'

import { useAuthorization } from '@resultadosdigitais/front-hub/react'
import { Badge } from '@resultadosdigitais/tangram-components'
import { Question, Apps, Bell } from '@resultadosdigitais/tangram-react-icons'

import { SecondaryMenu as Menu } from '@resultadosdigitais/crm-components'
import { toggleMessagesBar } from '@resultadosdigitais/crm-core'

import { useFronthubContext } from '../../services/context'
import Routes from '../../routes'

const Icon = styled.svg`
  width: 24px;
  height: 24px;
`

const Notification = styled(({ className }) => {
  const { notification: { unreadTotal = 0 } = {} } = useFronthubContext()
  const { t: translate } = useTranslation()

  const icon = <Bell title={translate('notifications')} titleId="idToHelperIcon" />

  const content = Number(unreadTotal)

  return content ? (
    <Badge className={className} content={content} maxValue={5}>
      {icon}
    </Badge>
  ) : (
    icon
  )
})`
  width: 18px;
  height: 18px;
`

const SecondaryMenu = () => {
  const { t: translate } = useTranslation()
  const { Allowed } = useAuthorization()

  return (
    <Routes>
      {({ routes }) => (
        <Menu aria-label="secondary-menu">
          <Menu.Link aria-label={translate('notifications_button')} tooltip={translate('notifications')} onClick={toggleMessagesBar}>
            <Icon as={Notification} />
          </Menu.Link>
          <Menu.Dropdown>
            <Menu.Dropdown.Trigger id="secondary-menu-help" tooltip={translate('help')}>
              <Icon as={Question} title={translate('help')} titleId="idToHelperIcon" />
            </Menu.Dropdown.Trigger>
            <Menu.Dropdown.List>
              <Menu.Dropdown.Item href={routes.helpCenter} target="_blank" title={translate('help_center')}>
                {translate('help_center')}
              </Menu.Dropdown.Item>
              <Allowed permission="secondaryMenu:adminTraining">
                <Menu.Dropdown.Item href={routes.adminTraining} target="_blank" title={translate('admin_training')}>
                  {translate('admin_training')}
                </Menu.Dropdown.Item>
              </Allowed>
              <Menu.Dropdown.Item href={routes.userTraining} target="_blank" title={translate('user_training')}>
                {translate('user_training')}
              </Menu.Dropdown.Item>
              <Menu.Dropdown.Item href={routes.demo} target="_blank" title={translate('demo')}>
                {translate('demo')}
              </Menu.Dropdown.Item>
            </Menu.Dropdown.List>
          </Menu.Dropdown>
          <Menu.Dropdown>
            <Menu.Dropdown.Trigger id="secondary-menu-products" tooltip={translate('products')}>
              <Icon as={Apps} title={translate('products')} titleId="idToProductIcon" />
            </Menu.Dropdown.Trigger>
            <Menu.Dropdown.List>
              <Menu.Dropdown.Item href={routes.marketing} target="_blank" title={translate('rd_station_marketing')}>
                {translate('rd_station_marketing')}
              </Menu.Dropdown.Item>
              <Menu.Dropdown.Item href={routes.appStore} target="_blank" title={translate('rd_station_app_store')}>
                {translate('rd_station_app_store')}
              </Menu.Dropdown.Item>
              <Menu.Dropdown.Item href={routes.marketplace} target="_blank" title={translate('rd_station_marketing_place')}>
                {translate('rd_station_marketing_place')}
              </Menu.Dropdown.Item>
              <Menu.Dropdown.Item href={routes.university} target="_blank" title={translate('rd_station_university')}>
                {translate('rd_station_university')}
              </Menu.Dropdown.Item>
            </Menu.Dropdown.List>
          </Menu.Dropdown>
        </Menu>
      )}
    </Routes>
  )
}

export default SecondaryMenu
