import React from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'

import { Refresh } from '@resultadosdigitais/tangram-react-icons'
import { Button } from '@resultadosdigitais/tangram-components'

import { useTranslation } from '../../services/formatter'

const TotalSales = styled(({ children, value, onRefreshClick, ...rest }) => {
  const { t } = useTranslation()

  return (
    <div aria-labelledby="total-sales" {...rest}>
      <small>{t('sold_this_month')}</small>
      <div className="row">
        <strong>{t('total_sales', { value })}</strong>
        <Button kind="tertiary" onClick={onRefreshClick}>
          <Refresh />
        </Button>
      </div>
    </div>
  )
})`
  position: relative;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  background-color: var(--color-white);
  font-size: var(--text-size-sm);
  padding: var(--size-spacing-04);
  overflow-wrap: anywhere; // pra impedir de esconder o botão caso o número seja muito grande
  cursor: default;

  .row {
    display: flex;
    align-items: baseline;
    flex-grow: 1;
  }

  small {
    width: 100%;
    font: var(--font-size-sm-semibold);
  }

  strong {
    flex: 1;
    font: var(--font-size-xl-semibold);
  }

  button {
    display: flex;
    padding: var(--size-spacing-02);
  }
`

TotalSales.displayName = 'TotalSales'

TotalSales.propTypes = {
  value: PropTypes.number,
}

export default TotalSales
