module.exports = {
  rbac: {
  role: function(context) {
      function toBoolean(input) {
        return typeof(input) === 'string' ? input === 'true' : input;
      }

      const isFree = toBoolean(context.user.isFree);
      const isAdmin = toBoolean(context.user.isAdmin);

      if(isFree && isAdmin) {
        return 'adminFree'
      } else if(!isFree && isAdmin) {
        return 'adminBasic'
      } else if (!isFree && !isAdmin) {
        return 'userBasic'
      } else {
        return 'userFree'
      }
    },
  permissions: {
  accountMenu: ["account","integration"],
  primaryMenu: ["crmLive","crmLiveFeature"],
  secondaryMenu: ["adminTraining"],
},
  grants: {
  userFree: [],
  adminFree: ["userFree","accountMenu:account","accountMenu:integration","primaryMenu:crmLive","secondaryMenu:adminTraining"],
  userBasic: ["userFree"],
  adminBasic: ["adminFree","userBasic","primaryMenu:crmLiveFeature"],
},
},
}