import React from 'react'
import styled from 'styled-components'

import { useAuthorization } from '@resultadosdigitais/front-hub/react'
import { Tag } from '@resultadosdigitais/tangram-components'
import { blockFeatureIfFreeAccount } from '@resultadosdigitais/crm-core'
import { PrimaryMenu as Menu } from '@resultadosdigitais/crm-components'

import { useTranslation } from '../../services/formatter'
import Routes from '../../routes'

const UpgradeTag = styled(({ className }) => (
  <Tag className={className} kind="primary">
    Upgrade
  </Tag>
))`
  display: inline-block;
  margin-left: 5px;
  line-height: 22px;
`

const PrimaryMenu = () => {
  const { t: translate } = useTranslation()
  const { Allowed, Denied } = useAuthorization()

  const openTrialModal = event => {
    blockFeatureIfFreeAccount('ANALISAR_CRM_LIVE', event)
  }

  return (
    <Routes>
      {({ routes, isActive }) => (
        <Menu aria-label="primary-menu">
          <Menu.Link href={routes.dealsWorkVision} active={isActive(routes.dealsWorkVision)}>
            {translate('opportunities')}
          </Menu.Link>
          <Menu.Link href={routes.organizations} active={isActive(routes.organizations)}>
            {translate('companies/clients')}
          </Menu.Link>
          <Menu.Link href={routes.contacts} active={isActive(routes.contacts)}>
            {translate('contacts')}
          </Menu.Link>
          <Menu.Link href={routes.tasks} active={isActive(routes.tasks)}>
            {translate('tasks')}
          </Menu.Link>
          <Menu.Dropdown>
            <Menu.Dropdown.Trigger id="primarymenu-analyze" role="button" active={isActive([routes.reportsPanel, routes.live])}>
              {translate('analyze')}
            </Menu.Dropdown.Trigger>
            <Menu.Dropdown.List>
              <Menu.Dropdown.Item href={routes.reportsPanel} active={isActive(routes.reportsPanel)}>
                {translate('reports')}
              </Menu.Dropdown.Item>
              <Allowed permission="primaryMenu:crmLive">
                <Allowed permission="primaryMenu:crmLiveFeature">
                  <Menu.Dropdown.Item href={routes.live} target="_blank" active={isActive(routes.live)}>
                    {translate('crm_live')}
                  </Menu.Dropdown.Item>
                </Allowed>
                <Denied permission="primaryMenu:crmLiveFeature">
                  <Menu.Dropdown.Item onClick={openTrialModal}>
                    {translate('crm_live')}
                    <UpgradeTag />
                  </Menu.Dropdown.Item>
                </Denied>
              </Allowed>
            </Menu.Dropdown.List>
          </Menu.Dropdown>
        </Menu>
      )}
    </Routes>
  )
}

export default PrimaryMenu
