import React, { Fragment } from 'react'
import styled from 'styled-components'
import { Button as ButtonStyle } from '@resultadosdigitais/tangram-components'
import { Person, Building, Case } from '@resultadosdigitais/tangram-react-icons'
import { Search } from '@resultadosdigitais/crm-components'
import { queryResults, openNewDealModal } from '@resultadosdigitais/crm-core'
import { useTranslation } from '../../services/formatter'

import Routes from '../../routes'

const PersonIcon = styled(Person)`
  width: 16px;
  height: 18px;
  min-width: 16px;
  min-height: 18px;
  margin-right: 16px;
`

const BuildingIcon = styled(Building)`
  width: 16px;
  height: 18px;
  min-width: 16px;
  min-height: 18px;
  margin-right: 16px;
`

const CaseIcon = styled(Case)`
  width: 15.75px;
  height: 18px;
  min-width: 15.75px;
  min-height: 18px;
  margin-right: 15px;
`

const Button = styled(ButtonStyle)`
  color: #03a9f4;
  font-style: normal;
  font-weight: var(--text-weight-semibold);
  font-size: var(--text-size-sm);
  line-height: var(--text-size-lg);
  text-align: center;
  letter-spacing: -0.01em;
  heigth: 32px;
  margin: 20px auto 16px;
`
const icons = {
  contact: <PersonIcon />,
  organization: <BuildingIcon />,
  deal: <CaseIcon />,
}

const getResultsAsync = async query => {
  const values = await queryResults(query)
  if (typeof values !== 'object' || !values.results) return []
  return values.results.filter(({ total }) => total)
}

export default props => {
  const { t: translate } = useTranslation()

  return (
    <Routes>
      {({ routes }) => (
        <Search {...props} id="search" queryResults={getResultsAsync}>
          <Search.ResultsLoading text={translate('querying_results')} />
          <Search.ResultsNotFound text={translate('no_results_for')}>
            <Button aria-label={translate('new_deal')} kind="tertiary" onClick={openNewDealModal}>
              {translate('new_deal')}
            </Button>
          </Search.ResultsNotFound>
          <Search.ResultsFound>
            {({ results }) => {
              return results.map(({ type, records }) => {
                return (
                  !!records.length && (
                    <Fragment key={type}>
                      <Search.ResultsFound.Title>{translate(type)}</Search.ResultsFound.Title>
                      {records.map(({ id, name }) => (
                        <Search.ResultsFound.Item key={id} href={`${routes[`${type}s`]}/${id}`}>
                          {icons[type]}
                          {name}
                        </Search.ResultsFound.Item>
                      ))}
                    </Fragment>
                  )
                )
              })
            }}
          </Search.ResultsFound>
        </Search>
      )}
    </Routes>
  )
}
